.logo {
  position: relative;
  display: block;
  max-width: 200px;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.logo__image {

  margin-left: 10px;
  height: auto;
  max-height: 100%;
  display: block;
}

@media (max-width: 992px) {
  .logo {
    width: 30%;
    max-width: initial;
  }
}
